}

.nav__resume {
    margin-top: 10px;
    background-color: var(--section-headlines);
    padding: .5rem 1.2rem;
    border-radius: 50px;
    display: block;
}

.nav__resume:hover,
.nav__resume:focus{
    color: white;
    background-color: var(--mid-grey);
    text-decoration: none;
    transition: ease .5s;

  }

.nav__resume:active,
.nav__resume:visited {
  color: white;
  transition: ease .5s;
}


.nav__toggle-btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    cursor: pointer;
    transition: all .5s cubic-bezier(0.175, 0.785, 0.12, 1.275);
} 

.nav__toggle-btn__burger {
    width: 30px;
    height: 2px;
    background: transparent;
    border-radius: 5px;
    transition: all .5s cubic-bezier(0.175, 0.785, 0.12, 1.275);
}

.nav__toggle-btn__burger::before,
.nav__toggle-btn__burger::after {
    content: '';
    position: absolute;
    width: 30px;
    height: 2px;
    background: white;
    border-radius: 5px;
    left: 0;
    transition: all .5s cubic-bezier(0.175, 0.785, 0.12, 1.275);
}

.nav__toggle-btn__burger::before {
    transform: translateY(-6px);
}

.nav__toggle-btn__burger::after {
    transform: translateY(6px);
}

.nav__toggle-btn.open .nav__toggle-btn__burger {
    background: transparent;
}

.nav__toggle-btn.open .nav__toggle-btn__burger::before {
    transform: rotate(45deg);
    background: var(--primary-blue);
}

.nav__toggle-btn.open .nav__toggle-btn__burger::after {
    transform: rotate(-45deg);
    background: var(--primary-blue);
}



.nav__show {
    display: block;
    transition: ease-in;
}


@media screen and (min-width: 768px) { 
    .nav__social--display {
        display: none;
